.phones-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;

  list-style: none;
}

.phones-list__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
}

@media (max-width: 550px) {
  .phones-list__link {
    font-size: 18px;
    line-height: 24px;
  }
}