@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Regular"), local("TTcommons-regular"), url("../fonts/TTCommons-Regular.woff2") format("woff2"), url("../fonts/TTCommons-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Medium"), local("TTcommons-medium"), url("../fonts/TTCommons-Medium.woff2") format("woff2"), url("../fonts/TTCommons-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Bold"), local("TTcommons-bold"), url("../fonts/TTCommons-Bold.woff2") format("woff2"), url("../fonts/TTCommons-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 360px;
  font-family: "TT Commons";
  font-size: 16px;
  line-height: 20px;
  color: #202020;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1280px;
  margin: 0 auto;
  min-width: 360px;
  padding-top: 20px;
  padding-left: 40px;
  padding-bottom: 20px;
  padding-right: 40px;
  background-color: transparent;
}

.header__logo-image {
  vertical-align: middle;
}

@media (max-width: 1280px) {
  .header__container {
    width: auto;
  }
}
@media (max-width: 870px) {
  .header__container {
    padding-top: 5px;
    padding-left: 20px;
    padding-bottom: 5px;
    padding-right: 20px;
  }
}
.main-nav__list {
  width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__list-link {
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;
  font-weight: 500;
  font-size: 18px;
  color: #202020;
  line-height: 24px;
  transition: all 0.2s linear;
}
.main-nav__list-link:hover {
  border-bottom-color: #F1864B;
}
.main-nav__list-link:active {
  color: #F1864B;
  border-bottom-color: #F1864B;
}

.main-nav__button {
  display: none;
  width: 50px;
  height: 50px;
  border: none;
  background-color: transparent;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.main-nav__button--closed {
  background-image: url("../../img/icon-closed.svg");
}

.main-nav__button--opened {
  background-image: url("../../img/icon-opened.svg");
}

@media (max-width: 870px) {
  .main-nav {
    order: 3;
  }
  .main-nav > .main-nav__list {
    display: none;
  }
  .main-nav--show > .main-nav__list {
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .main-nav--show .main-nav__list-item {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #eeeeee;
    text-align: center;
  }
  .main-nav--show .main-nav__list-item:first-child {
    border-top: 1px solid #eeeeee;
  }
  .main-nav__button {
    display: block;
  }
}
.phones-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}

.phones-list__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
}

@media (max-width: 550px) {
  .phones-list__link {
    font-size: 18px;
    line-height: 24px;
  }
}
.promo {
  margin-top: -110px;
  background-image: url("../../img/icon-bg-promo-right.svg"), url("../../img/icon-bg-promo-left.svg");
  background-repeat: no-repeat;
  background-position: top 50px right -100px, top -350px left -200px;
}

.promo__container {
  width: 1440px;
  min-height: 600px;
  margin: 0 auto;
  padding-top: 200px;
  padding-right: 40px;
  padding-bottom: 100px;
  padding-left: 40px;
  background-image: url("../../img/bg-promo-girl.png"), url("../../img/icon-bg-promo-center.svg"), url("../../img/icon-bg-promo-compass.svg");
  background-repeat: no-repeat;
  background-position: top 100px right, top 100px right, top 450px left 80px;
}

.promo__title {
  width: 630px;
  margin: 0;
  margin-bottom: 20px;
  margin-left: 230px;
  padding: 0;
  font-weight: 700;
  font-size: 96px;
  line-height: 96px;
  color: #454848;
  text-transform: uppercase;
}

.promo__subtitle {
  margin: 0;
  margin-left: 230px;
  bottom: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #454848;
}

.promo__tours {
  width: 900px;
  margin: 0 auto;
  margin-top: 50px;
  border-radius: 5px;
}

@media (max-width: 1440px) {
  .promo__container {
    width: auto;
  }
}
@media (max-width: 1330px) {
  .promo__title {
    margin-left: 130px;
  }
  .promo__subtitle {
    margin-left: 130px;
  }
}
@media (max-width: 1220px) {
  .promo__tours {
    width: auto;
  }
  .promo__title {
    margin-left: 60px;
    font-size: 76px;
    line-height: 86px;
  }
  .promo__subtitle {
    margin-left: 60px;
  }
}
@media (max-width: 1010px) {
  .promo__title {
    margin-left: 0;
  }
  .promo__subtitle {
    margin-left: 0;
  }
}
@media (max-width: 700px) {
  .promo__title {
    width: 320px;
    font-size: 40px;
    line-height: 50px;
  }
}
.about__container {
  width: 1280px;
  margin: 0 auto;
  padding-right: 40px;
  padding-bottom: 140px;
  padding-left: 40px;
  text-align: center;
}

.about__title {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #454848;
}

.about__subtitle {
  width: 790px;
  margin: 0 auto;
  padding: 0;
}

@media (max-width: 1280px) {
  .about__container {
    width: auto;
  }
}
@media (max-width: 860px) {
  .about__subtitle {
    width: auto;
  }
}
.booking-wrap {
  position: relative;
  padding-top: 80px;
  overflow: hidden;
}

.booking {
  background-image: url("../../img/icon-bg-booking.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.booking::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-40%);
  width: 611px;
  height: 167px;
  background-image: url("../../img/airplane.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
}

.booking__container {
  position: relative;
  width: 1280px;
  margin: 0 auto;
  min-height: 1104px;
  margin: 0 auto;
  padding-top: 190px;
  padding-right: 40px;
  padding-bottom: 80px;
  padding-left: 40px;
}
.booking__container::before {
  content: "";
  position: absolute;
  top: 300px;
  right: -280px;
  width: 700px;
  height: 419px;
  background-image: url("../../img/bg-booking-palm.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 700px auto;
  transform: rotate(30deg);
  z-index: 10;
}
.booking__container::after {
  content: "";
  position: absolute;
  bottom: 50px;
  left: -70px;
  width: 180px;
  height: 126px;
  background-image: url("../../img/bg-booking-shell.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: 10;
}

.booking__title {
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #FFFFFF;
  text-align: center;
}

.booking__subtitle {
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 0;
  width: 792px;
  color: #025472;
  text-align: center;
}

.booking__image-container {
  position: absolute;
  top: 400px;
  left: 40px;
  width: 691px;
  height: 588px;
  border-radius: 25px;
  overflow: hidden;
  z-index: 1;
}

.booking__image {
  width: 691px;
  height: 588px;
  overflow: hidden;
}

.booking__content {
  position: relative;
  width: 590px;
  min-height: 469px;
  margin-top: 110px;
  margin-left: auto;
  padding: 40px 25px 30px;
  border-radius: 22px;
  box-shadow: -12px 6px 34px 3px rgba(0, 0, 0, 0.13);
  background-color: #FFFFFF;
  z-index: 10;
}

.booking-list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  list-style: none;
}

.booking-list__item {
  position: relative;
}
.booking-list__item::before {
  content: "";
  position: absolute;
  top: 5px;
  right: 10px;
  width: 24px;
  height: 24px;
  background-image: url("../../img/icon-arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.booking-list__item--active::before {
  background-image: url("../../img/icon-arrow-up.svg");
}

.booking-list__button {
  position: relative;
  display: inline-block;
  margin-bottom: 10px;
  padding-bottom: 5px;
  padding-left: 20px;
  border: none;
  background-color: transparent;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #F1864B;
  vertical-align: middle;
}
.booking-list__button::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 2px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: #F1864B;
}

.booking-list__text {
  display: none;
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
}

.booking-list__text--show {
  display: block;
}

.booking__button {
  width: 225px;
  min-height: 44px;
  padding: 13px 50px;
  border: none;
  border-radius: 4px;
  background-color: #F1864B;
  color: #FFFFFF;
  transition: 0.2s linear;
}
.booking__button:hover {
  background-color: #E06B2A;
}
.booking__button:active {
  color: #F1864B;
}

@media (max-width: 1280px) {
  .booking__container {
    width: auto;
  }
}
@media (max-width: 900px) {
  .booking__subtitle {
    width: auto;
  }
}
@media (max-width: 790px) {
  .booking::before {
    top: 50px;
    width: 300px;
    height: 82px;
    transform: translateX(-55%);
  }
  .booking__container {
    padding-top: 80px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .booking__container::before {
    background: none;
  }
  .booking__container::after {
    bottom: 30px;
    left: 70%;
  }
  .booking__image-container {
    width: auto;
    height: 450px;
    top: 280px;
    left: 0;
    border-radius: 0px;
  }
  .booking__image {
    width: 100%;
    height: auto;
  }
  .booking__content {
    width: 100%;
    margin: 0 auto;
    margin-top: 400px;
    border-radius: 10px;
  }
}
@media (max-width: 530px) {
  .booking__container {
    min-height: 400px;
    padding-bottom: 80px;
  }
  .booking__container::after {
    bottom: 20px;
    background-size: 100px auto;
  }
  .booking__title {
    font-size: 36px;
    line-height: 42px;
  }
  .booking__content {
    margin-top: 300px;
    min-height: 300px;
  }
  .booking-list {
    margin-bottom: 20px;
  }
  .booking-list__button {
    padding-bottom: 0;
    font-size: 20px;
    line-height: 24px;
  }
}
@media (max-width: 460px) {
  .booking__content {
    margin-top: 200px;
  }
}
